const hkSC = uni.requireNativePlugin('HKing-SmartConfig'); 
export default {
	data() {
		return {
			ssid:"",
			password:0,
	
		}
	},
	onLoad() {

	},
	methods: {
		inputpassword(e) {
		                this.password = e.detail.value;
						  console.log(this.password)
		            },
		getssid(){
			var apSsid = "";
			hkSC.getWifiSsid(result=>{
			    if(result.apSsid==""){
			        console.log("当前手机未连接wifi");
			        return;
			    }
			   this.ssid = result.apSsid;
			    console.log("apSsid："+this.ssid);
			});	
		},
		startwifi(){
			console.log('ccccccc'+this.ssid)
			if(this.ssid!=""){
				
				if(this.password==0)
				{
					uni.showToast({
					    title: '密码不能为空',
					
					    duration: 2000
					});
					
					
				}
				if(this.password!=0)
				{
					uni.showLoading({
					    title: '配网中请稍后..'
					});
					hkSC.startSmaerConfig(this.ssid,this.password,result=>{
					    uni.hideLoading();
					   uni.showToast({
					       title: '配网成功',
					   
					       duration: 2000
					   });
					  this.ssid="";
					});
					
				
				}
				
			}
			if(this.ssid=="")
			{
				uni.showToast({
				    title: 'WIFI名未知',
				    duration: 2000
				});
			}
			
			
		}
		
	}
}
